@import url(https://fonts.googleapis.com/css2?family=Signika:wght@400;700&display=swap);

.about__container{min-height:100vh;margin-bottom:15rem}.about__content{display:flex}.profile__picture__container{display:flex}.profile__picture{min-width:20rem;min-height:20rem;border:4px solid #fff;background-size:100% 100%;background-color:rgba(0,0,0,.75);background-blend-mode:multiply;transition:all .3s ease-in-out;flex:25% 1}.profile__picture:hover{transition:all .3s ease-in-out;background-color:rgba(0,0,0,0)}.about__information{flex:65% 1}.about__information p{font-size:1.2rem;margin:1rem}.about__information ul{margin:1rem}.about__information ul li:before{content:"▸ "}.list__container{display:grid;grid-template-columns:repeat(2, 1fr)}
.footer__container{width:100%;display:flex;margin-top:15rem}.footer__content{margin-top:2em;display:flex;flex-direction:column;justify-content:center;align-items:center}.header__creator{margin:auto;text-align:center}.header__creator p{font-size:.9rem;letter-spacing:.2rem;margin-bottom:0}.header__creator span{color:#149651}
.wrapper{max-width:1000px;min-width:200px;margin:0 auto;margin-bottom:1em}.hero__content{text-align:center;min-height:100vh;display:flex;flex-direction:column;justify-content:center}.hero__content .name{font-weight:bold;font-size:3em;margin-top:0}.hero__content .subtitle{font-size:1.6em;font-weight:bold;margin-top:-1em;color:#149651}
header{display:flex;justify-content:space-between;padding:1em;align-items:center;justify-content:space-between;background:#070024;position:-webkit-sticky;position:sticky;top:0;z-index:1;box-shadow:rgba(0,0,0,.25) 0px 25px 50px -12px}header img{width:50px}header .navbar__logo{border:2px solid #149651;width:3em;height:3em;text-align:center;line-height:3em;color:#fff}header .navbar__logo span{font-weight:bold;font-size:2em}header .navbar__logo:hover{cursor:pointer;color:#149651;border:2px solid #fff}header ul{display:flex}header ul li{padding:1em}header ul li:hover{border-bottom:1px solid #149651;transform:translateY(-2px)}header ul li p{padding:0 1em}header .navbar__button{text-align:center;padding:1em 0;width:10em;border:2px solid #149651}header .navbar__button:hover{color:#149651;border:2px solid #fff;cursor:pointer}.header__container{text-align:center;padding:1em;width:50%;margin:auto;margin-bottom:5em}
.project__title{text-align:center}.repo__card__container{display:grid;grid-template-columns:repeat(3, 1fr);grid-row-gap:2.5rem;row-gap:2.5rem;grid-column-gap:2.5rem;-webkit-column-gap:2.5rem;column-gap:2.5rem;width:75%;margin:auto}.repo__item{background-color:rgba(255,255,255,.8);border:1px solid rgba(0,0,0,.8);padding:20px;font-size:15px;text-align:center}.repo__item:hover{transition:all .3s ease-in-out;background-color:rgba(15,28,102,.808);transform:translateY(-2px)}
.sidebar__container{position:fixed;bottom:0;transition:all .3s ease-in-out}.sidebar__container:after{content:"";display:block;height:90px;width:1px;margin:0 auto;background-color:#fff}.sidebar__container:before{content:"";display:block;height:50rem;width:1px;margin:0 auto;background-color:#fff}.left{left:1.5rem}.left ul{display:flex;flex-direction:column;align-items:center;align-content:center;justify-content:center;text-align:center}.left li{padding-top:1.2em}.left li:last-of-type{margin-bottom:1em}.left li:not(:last-child){margin-bottom:.5em}.left li a{display:block}.left a{font-size:1.5em}.left a:hover,.left a:focus{color:#149651;transition:all .3s ease-in-out;transform:translateY(-5px)}.right{right:1.5rem}.right h5{color:#fff;letter-spacing:.4em;-webkit-writing-mode:vertical-rl;-ms-writing-mode:tb-rl;writing-mode:vertical-rl;font-size:1em;margin:20px auto}.right h5:hover,.right h5:focus{color:#149651;cursor:pointer;transform:translateY(-3px);transition:all .3s ease-in-out}
.resume__content{margin-top:10em}.resume__content img{width:100%}.go__back{font-size:2.5em}
* {
    box-sizing: border-box;
}

body {
    margin: 0;
    color: white;
    font-family: 'Signika', sans-serif;
    background: #070024;
}

a {
    color: white;
    text-decoration: none;
}

ul {
    list-style: none;
    margin: 0;
    padding: 0;
}
@media(max-width: 345px){body{display:none}}@media(max-width: 445px){.project__splitter .left__half{display:none}.project__splitter .left__half .image{display:none}.project__splitter .right__half .project__description{display:none}.project__splitter .right__half .project__languages{display:none}.header__creator p{overflow:hidden}}@media(max-width: 599px){.wrapper{width:70%}header{display:none}.mobile__navbar{display:flex;position:fixed;left:1rem;font-size:1.5em;cursor:pointer}.left{left:1em}.right{right:1em}.about__content{flex-wrap:wrap-reverse}.about__content .profile__picture{margin-bottom:1rem;flex:100% 1}.header__creator p{font-size:.7rem}}@media(min-width: 600px){.wrapper{width:80%}.about__content .profile__picture{max-height:13rem;max-width:25rem}.project__header:after{width:35%}.project__header:before{width:35%}}

